import lModal from '@/components/l-modal/l-modal.vue'
export default {
	components: {
		lModal,
	},
	data() {
		return {
			userLogin: true, // 用户是否登录
			isVip: true, // 是否是vip
			isShowModal: false,
			modalTitle: '提示',
		}
	},
	methods: {
		gotoUrl(url){
			uni.navigateTo({
				url:url
			})
		},
		gotoTab(url){
			uni.switchTab({
				url: url
			})
		},
		gotoReturn(){
			this.isShowModal = true
		},
		cancel(){
			this.isShowModal = false
		},
		confirm() {
			this.isShowModal = false
			uni.clearStorage();
			uni.showToast({
				icon: 'success',
				title: '退出登录成功！',
				duration: 2000
			})
			setTimeout(() => {
				uni.navigateTo({
					url: '/pages/myData/loadPage'
				})
			}, 1500)
		},
	}
}
